<template>
  <article class="portfolio hentry">
    <header class="entry-header">
      <a target="_blank" v-bind:href="'/detail/'+link"><h1 class="entry-title">{{ title }}</h1></a>
      <div class="entry-thumbnail" style="text-align: center;">
        <a target="_blank" v-bind:href="'/detail/'+link">
          <img width="800" height="533" v-bind:src="imageSrc" v-bind:alt="title" class="attachment-post-thumbnail size-post-thumbnail wp-post-image" />
        </a>
      </div>
    </header>
    <div class="entry-content">
     <p id="description"></p>
    </div>
  </article>
  <bottomMenuHome />
</template>

<script>
import bottomMenuHome from '@/components/HomeComponent/BottomMenuHome';
import firebase from 'firebase';
export default {
  components: { bottomMenuHome },
  data(){
    return{
      description: '...',
      imageSrc: '/img/icons/loading.gif'
    }
  },
  computed: {
    link(){ return this.$route.params.id; },
    title(){
      window.scroll(0,0)
      let titlePage = this.$route.params.id.replaceAll('+', ' ');
      document.title = titlePage;

      firebase.database().ref().child("art/"+titlePage).on('value', (snapshot) => {
        let itemVal = snapshot.val().image;
            itemVal = itemVal.replace('http://www.site101.mir915bcf08b.comcb.info/', 'https://alexandr-shop.000webhostapp.com/images/?img=');
            itemVal = itemVal.replace('https://www.kasparov.ru/', 'https://alexandr-shop.000webhostapp.com/images/?img=');
            itemVal = itemVal.replace('http://www.kasparov.ru/', 'https://alexandr-shop.000webhostapp.com/images/?img=');
        this.imageSrc = itemVal;
      });

      firebase.firestore().collection("art").doc(titlePage).get().then(doc => {
        document.getElementById('description').innerHTML = doc.data().description;
      });

      return titlePage;
    },

  }
}
</script>

<style scoped>
h1{
  margin-top: 22px;
  margin-bottom: 22px;
}
#description{
  color: black;
}
</style>